@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1200px;
  }
}
.page #banner {
  position: relative;
}
.page #banner .texts {
  position: absolute;
  bottom: 20%;
  font-weight: bold;
}
.page #banner .texts h1 {
  font-size: 30px;
  line-height: 37px;
  color: #fff;
  margin-bottom: 3px;
  font-weight: bold;
}
.page #banner .texts h1 span {
  color: #cae098;
}
.page #banner .texts p {
  font-size: 20px;
  line-height: 24px;
  color: #fff;
}
.page#page-sobre-nos .container-sobre {
  position: relative;
  background: #006635;
  z-index: 0;
}
.page#page-sobre-nos .container-sobre.show-before::before {
  content: "";
  position: absolute;
  top: 2%;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../../assets/images/before_sobre_sec1.png") no-repeat left bottom;
  background-size: contain;
  opacity: 0.2;
  z-index: -1;
}
.page#page-sobre-nos .container-sobre.show-after::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background: url("../../assets/images/before-section.png") no-repeat left bottom;
  background-size: cover;
  opacity: 0.07;
  filter: invert(1);
  -webkit-mask-image: linear-gradient(to top, rgb(0, 0, 0), rgba(0, 0, 0, 0));
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: cover;
  mask-image: linear-gradient(to top, rgb(0, 0, 0), rgba(0, 0, 0, 0));
  mask-repeat: no-repeat;
  mask-size: cover;
  z-index: -2;
}
.page#page-sobre-nos .container-sobre.show-after::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 20%;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.5), transparent);
  z-index: -1;
}
.page#page-sobre-nos .container-sobre .compromisso {
  background-color: rgba(0, 0, 0, 0.22);
  border-radius: 5px;
  padding: 45px;
}
.page#page-sobre-nos .container-sobre .compromisso p {
  margin-bottom: 0;
}
.page#page-sobre-nos #video {
  background: #d9d9d9;
  padding-bottom: 80px;
}
.page#page-nossa-historia .container-sobre {
  position: relative;
  background: #006635;
  z-index: 0;
}
.page#page-nossa-historia .container-sobre::before {
  content: "";
  position: absolute;
  top: 2%;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../../assets/images/before_sobre_sec1.png") no-repeat left bottom;
  background-size: contain;
  opacity: 0.2;
  z-index: -1;
}
.page#page-nossa-historia .container-marcos {
  position: relative;
  background: #006635;
  z-index: 0;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 70%;
  height: 650px;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-snap-type: x mandatory;
  padding-left: 7px;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item .content-marco {
  position: relative;
  height: 321.5px;
  gap: 30px;
  cursor: pointer;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item .content-marco .dot-top {
  content: "";
  position: absolute;
  top: calc(20% - 15px);
  left: -7px;
  width: 15px;
  height: 15px;
  background-color: #006635;
  border: 2px solid #fff;
  border-radius: 50%;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item .content-marco .dot-bottom {
  content: "";
  position: absolute;
  bottom: -7.5px;
  left: -7px;
  width: 15px;
  height: 15px;
  background-color: #006635;
  border: 2px solid #fff;
  border-radius: 50%;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item .content-marco::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 1px;
  height: 80%;
  background-color: #cae098;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(odd) {
  align-items: flex-start;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(odd) .content-marco {
  padding: 0 60px 30px 60px;
  border-bottom: 1px solid #cae098;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(odd) .content-marco .texts {
  justify-content: center;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(even) {
  align-items: flex-end;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(even) .content-marco {
  padding: 30px 60px 0 60px;
  border-top: 1px solid #cae098;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(even) .content-marco .dot-top {
  top: -7.5px;
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(even) .content-marco .dot-bottom {
  bottom: calc(20% - 15px);
}
.page#page-nossa-historia .container-marcos .marcos .marcos-wrapper .marco-item:nth-child(even) .content-marco::before {
  top: 0;
  bottom: inherit;
}
.page#page-nossa-historia .container-marcos::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../../assets/images/before-section.png") no-repeat left bottom;
  background-size: cover;
  opacity: 0.03;
  filter: invert(1);
  z-index: -1;
}